.Player {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 1rem;
  text-align: left;
  background-color: #ecf0f1;
}

.Player + .Player {
  text-align: right;
}

.Player.dead {
  opacity: .5;
  -webkit-transition: opacity .5s ease-in-out;
  -o-transition: opacity .5s ease-in-out;
  transition: opacity .5s ease-in-out;
}

.Player-title {
  margin-top: 0;
  margin-bottom: .5rem;
  padding: .25rem;
  font-size: 1.3rem;
  font-weight: 300;
}

.Player-life {
  display: inline-block;
  width: 100%;
  padding: .25rem;
  font-size: 1.1rem;
  color: #ecf0f1;
  background-color: #27ae60;
  -webkit-transition: width .2s ease;
  -o-transition: width .2s ease;
  transition: width .2s ease;
}

.Player.dead .Player-life {
  padding: .25rem 0;
}

.Player-life.warning {
  background-color: #e67e22;
}

.Player-life.danger {
  background-color: #e74c3c;
}

.Player-sprite-1 {
  width: 64px;
  height: 64px;
  margin-left: auto;
  background-image: url("../img/player1.png");
}

.Player-sprite-2 {
  width: 64px;
  height: 64px;
  background-image: url("../img/player2.png");
}

.walk-right {
  -webkit-animation: walk-right .8s steps(8) infinite;
          animation: walk-right .8s steps(8) infinite;
}

.atack-right {
  -webkit-animation: atack-right .8s steps(5) infinite;
          animation: atack-right .8s steps(5) infinite;

  background-position: 0 -960px;
}

.walk-left {
  -webkit-animation: walk-left .8s steps(8) infinite;
          animation: walk-left .8s steps(8) infinite;
}

.atack-left {
  -webkit-animation: atack-left .8s steps(5) infinite;
          animation: atack-left .8s steps(5) infinite;
}

.dead {
  background-position: -330px -1286px;
  -webkit-animation: dead .5s steps(5) 1;
          animation: dead .5s steps(5) 1;
}

@-webkit-keyframes walk-left {
  from {
    background-position: 0 -580px;
  }

  to {
    background-position: -512px -580px;
  }
}

@keyframes walk-left {
  from {
    background-position: 0 -580px;
  }

  to {
    background-position: -512px -580px;
  }
}

@-webkit-keyframes atack-left {
  from {
    background-position: 0 -836px;
  }

  to {
    background-position: -320px -836px;
  }
}

@keyframes atack-left {
  from {
    background-position: 0 -836px;
  }

  to {
    background-position: -320px -836px;
  }
}

@-webkit-keyframes walk-right {
  from {
    background-position: 0 -706px;
  }

  to {
    background-position: -512px -706px;
  }
}

@keyframes walk-right {
  from {
    background-position: 0 -706px;
  }

  to {
    background-position: -512px -706px;
  }
}

@-webkit-keyframes atack-right {
  from {
    background-position: 0 -964px;
  }

  to {
    background-position: -320px -964px;
  }
}

@keyframes atack-right {
  from {
    background-position: 0 -964px;
  }

  to {
    background-position: -320px -964px;
  }
}

@-webkit-keyframes dead {
  from {
    background-position: 0px -1286px;
  }

  to {
    background-position: -330px -1286px;
  }
}

@keyframes dead {
  from {
    background-position: 0px -1286px;
  }

  to {
    background-position: -330px -1286px;
  }
}



